* {
  margin: 0;
  padding: 0; }

html, body {
  height: 100%;
  background-color: lightgray; }

.grid-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

.unfaithfulDogs {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: navy;
  font-size: 400%;
  padding-top: 15%; }

.sentence {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: black;
  font-size: 150%; }

#title {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: lightseagreen; }
  #title h2 {
    font-size: 450%;
    padding-top: 15%; }
  #title h6 {
    font-size: 200%; }
